import request from '@/apis/request.wms'

// 列表
export function getListApi(data) {
  return request({
    url: '/basic/unit/pageList',
    method: 'post',
    data
  })
}
// 下拉列表 参数：unitType 单位类型 （取值为单位类型字典值）
export function getPullListItemApi(data) {
  return request({
    url: '/basic/unit/listItem',
    method: 'post',
    data
  })
}
// 新增
export function getAddApi(data) {
  return request({
    url: '/basic/unit/add',
    method: 'post',
    data
  })
}
// 修改
export function getEditApi(data) {
  return request({
    url: '/basic/unit/edit',
    method: 'post',
    data
  })
}
// 删除
export function getDeleteApi(data) {
  return request({
    url: '/basic/unit/delete',
    method: 'post',
    data
  })
}

